import React from 'react';
import './Hand.css';

const PI = Math.PI;

export interface HandProps {
  cx: number;
  cy: number;
  length: number;
  width: number;
  angle: number;
}

export default ({cx, cy, length, width, angle}: HandProps) => {
  const angleOffset = PI / 2;
  const x2 = Math.cos(2 * PI * angle / 360 - angleOffset) * length + cx;
  const y2 = Math.sin(2 * PI * angle / 360  - angleOffset) * length + cy;

  return (
     <line x1={cx} y1={cy} x2={x2} y2={y2} stroke="#89E4DD"  strokeWidth={width} />
  );
}