import React from "react";
import Hand from "./Hand";
import "./Clock.css";

export interface ClockProps {
  time: Date;
  cx: number;
  cy: number;
  r: number;
}

export default ({ time, cx, cy, r }: ClockProps) => {
  const seconds = time.getSeconds();
  const minutes = time.getMinutes();
  const hours = time.getHours();
  const secondAngle = (seconds / 60) * 360;
  const minutesAngle = (minutes / 60) * 360 + secondAngle / 60;
  const hoursAngle =
    (hours / 12) * 360 + minutesAngle / 60 + secondAngle / 3600;

  return (
    <>
      <svg width="300" height="300" style={{ backgroundColor: 'transparent' }}>
        {/* <defs>
          <pattern
            id="image1"
            x="0"
            y="0"
            patternUnits="userSpaceOnUse"
            height="1"
            width="1"
          >
            <image
              x="0"
              y="0"
              height="100"
              width="100"
              xlinkHref="https://i.dlpng.com/static/png/376211_preview.png"
            ></image>
          </pattern>
        </defs> */}
        <defs>
          <pattern
            id="image"
            patternUnits="userSpaceOnUse"
            height="300"
            width="300"
          >
            <image
              className="clockImg"
              x="0"
              y="0"
              xlinkHref="/images/clock.png"
            ></image>
          </pattern>
        </defs>
        <circle fill="url(#image)" stroke="#444" cx={cx} cy={cy} r={r} />
        <Hand cx={cx} cy={cy} length={r * 0.75} width={3} angle={secondAngle} />
        <Hand cx={cx} cy={cy} length={r * 0.7} width={5} angle={minutesAngle} />
        <Hand cx={cx} cy={cy} length={r * 0.6} width={8} angle={hoursAngle} />
      </svg>
    </>
  );
};
